export const getClearPathName = (pathName) => {
    const clearPathName = pathName.slice(pathName.lastIndexOf("/"));
    if (clearPathName.length > 1)
        return clearPathName.slice(1);
    return clearPathName;
}

export const setScrollOff = (mobileSize) => {
    if (window.outerWidth < mobileSize) {
        document.querySelector("body").style.overflow = "hidden";
    } else {
        const $el = document.querySelector(".ScrollbarsCustom-Scroller");
        $el.style = `${$el.style.cssText} overflow: hidden !important`;
    }
}

export const setScrollOn = (mobileSize) => {
    if (window.outerWidth < mobileSize) {
        document.querySelector("body").style.overflow = "auto";
    } else {
        document.querySelector(".ScrollbarsCustom-Scroller").style.overflow = "";
    }
}

export const handleClickToTop = () => {
    window.scrollTo({
        behavior: "smooth",
        top: 0
    }) || document.getElementsByClassName("ScrollbarsCustom-Scroller")[0].scrollTo({behavior: "smooth", top: 0})
}